.gray-bg {
    background: #F4F7FC;
}
.pt-10{
    padding-top: 7rem !important;
}
.pb-10{
    padding-bottom: 10rem !important;
}

.section-title span {
    font-size: 25px;
    font-weight: 600;
    display: block;
    color: #E44E83;
    margin-bottom: 12px;
}

.section-title h2{
    font-size: 45px;
    font-weight: 700;
    color: #313450;
    margin: 0px;
}
.p{
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    color: #6B6F92;
    margin: 0px;
}
.icon-gradient {
    position: relative;
    width: 69px;
    height: 69px;
    line-height: 70px;
    border-radius: 50%;
    color: #fff;
    margin-left: auto;
    margin-right: auto;
    font-size: 28px;
    z-index: 1;
}
.mb-30 {
    margin-bottom: 30px;
}
.gradient-1 {
    background-image: linear-gradient(#6240c8 0%, #a145fe 100%);
}
.gradient-2 {
    background-image: linear-gradient(#b548f2 0%, #d283fd 100%);
}
.gradient-3 {
    background-image: linear-gradient(#e44e83 0%, #ffb8d1 100%);
}
.gradient-4 {
    background-image: linear-gradient(#40b4f6 0%, #79cefd 100%);
}
.icon-gradient::after {
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-image: inherit;
    opacity: .15;
    z-index: -1;
    left: 0;
    top: 0;
    transform: scale(1.3);
    transition: all 0.3s ease-out 0s;
}
.single-service:hover .service-icon::after {
    transform: scale(1);
}

.single-service h4{
    font-size: 25px;
    font-weight: 700;
    color: #313450;
    margin: 0px;
    margin-bottom: 15px;
}
