.mb-150 {
    margin-bottom: 150px;
}
.process-section {
    position: relative;
    z-index: 1;
}

.process-section .shape {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: -1;
}
@media only screen and (min-width: 768px) and (max-width: 991px)
{
    .process-section .shape {
        bottom: -50px;
    }
}
@media (max-width: 767px)
{
    .process-section .process-img img {
        width: 100%;
    }
    .process-section .shape {
        bottom: -30px;
        width: 50%;
    }
}