.why-use-section {
    position: relative;
    z-index: 1;
}
.pt-150 {
    padding-top: 150px;
}
.why-use-section .shape {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: -1;
}
.why-use-section .why-use-img {
    margin-left: -150px;
}

.accordion .faq-item {
    border: none;
    box-shadow: 0px 0px 34px rgb(175 175 175 / 16%);
}

.mb-15 {
    margin-bottom: 15px;
}
.accordion .accordion-button{
    padding: 20px 33px 20px 25px;
}
.accordion .accordion-button i{
    width: 59px;
    height: 59px;
    border-radius: 50%;
    text-align: center;
    line-height: 62px;
    color: #fff;
    display: block;
    font-size: 25px;
    position: relative;
    z-index: 1;
    margin-right: 20px;
    float: left;
}
.accordion .accordion-button i::after{
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    border-radius: 50%;
    background-image: inherit;
    transform: scale(1.3);
    opacity: .15;
    z-index: -1;
}
.accordion .accordion-button span{
    font-size: 25px;
    font-weight: 700;
    color: #313450;
    display: block;
    
}
.accordion .accordion-button:focus{
    box-shadow: none;
    background-color: #fff;
    background-image: none;
}

.accordion .accordion-button::after{
    content: '';
    position: absolute;
    width: 0;
    height: 0;
    border-top: 6px solid #E44E83;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    right: 25px;
    top: 50%;
    background-image: none;
    margin-top: -3px;
    transform: rotate(180deg);
    transition: all 0.3s ease-out 0s;
}
.accordion .accordion-button.collapsed::after{
    transform: rotate(
0deg);
    border-top-color: #6B6F92;
}
@media only screen and (min-width: 992px) and (max-width: 1199px)
{
    .why-use-section .why-use-img img {
        width: 100%;
    }
}
@media only screen and (min-width: 768px) and (max-width: 991px)
{
    .why-use-section .faq-column {
        order: -1;
    }
    .why-use-section .why-use-img {
        margin-left: 0px;
    }
    .why-use-section .why-use-img img {
        width: 100%;
    }
    .why-use-section .shape {
        bottom: -50px;
    }
}
@media (max-width: 767px)
{
    .why-use-section .faq-column {
        order: -1;
    }
    .why-use-section .why-use-img {
        margin-left: 0px;
    }
    .why-use-section .why-use-img img {
        width: 100%;
    }
    .why-use-section .shape {
        width: 50%;
        bottom: -30px;
    }
    .accordion .accordion-button i{
        width: 45px;
        height: 45px;
        line-height: 48px;
        font-size: 20px;
    }
    .accordion .accordion-button span{
        font-size: 18px;
    }
}