.pb-120 {
    padding-bottom: 120px;
}
.pt-150 {
    padding-top: 150px;
}

.mb-55 {
    margin-bottom: 55px;
}
.mb-15 {
    margin-bottom: 15px;
}

.single-team .team-img {
    position: relative;
    overflow: hidden;
    border-radius: 10px;
}
.mb-20 {
    margin-bottom: 20px;
}
.single-team .team-img img {
    width: 100%;
}

.single-team .team-img .team-social-links {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 0px;
    display: flex;
    background: #E44E83;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: .3s;
    border-radius: 0 0 10px 10px;
}
.single-team:hover .team-social-links {
    opacity: 1;
    visibility: visible;
    height: 65px;
}
.single-team .team-img .team-social-links ul{
    padding:0;
    margin: 0;
}
.single-team .team-img .team-social-links ul li {
    display: inline-block;
}
.single-team .team-img .team-social-links ul li a {
    color: #fff;
    font-size: 16px;
    margin: 0 10px;
}

.single-team .team-info span {
    font-size: 18px;
    color: #313450;
}