*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
nav.navbar{
    transition: all 0.3s ease-out 0s;
    padding: 16px 30px;
    background: rgba(255, 255, 255, 0.08);
    border-radius: 10px;
}
.sticky nav.navbar{
    padding: 16px 30px;
    background: #fff;
    box-shadow: 0 0 10px rgb(34 34 34 / 5%);

}
.navbar-area.sticky .img-1{
    display: none;
}
.navbar-area.sticky .img-2{
    display: block;
}
.navbar-area .img-2{
    display: none;
}
.navbar-area.sticky{
    position: fixed;
    z-index: 99;
    top: 10px;
    transition: all 0.3s ease-out 0s;

}
.navbar-area.sticky .navbar-toggler .toggler-icon{
    background-color: #000;
}
.sticky .navbar-nav .nav-item .nav-link{
    color: #313450;
}
.sticky .navbar-nav .nav-item .nav-link:hover{
    color: #E44E83;
}
.navbar-area {
    position: absolute;
    top: 30px;
    left: 0;
    width: 100%;
    z-index: 99;
    transition: all 0.3s ease-out 0s;
}
.navbar-toggler .toggler-icon {
    width: 30px;
    height: 2px;
    background-color: #fff;
    display: block;
    margin: 5px 0;
    position: relative;
    transition: all 0.3s ease-out 0s;
}
.navbar .navbar-toggler:focus{
    box-shadow: none;
}
.navbar-toggler[aria-expanded=true] .toggler-icon:nth-of-type(1) {
    transform: rotate(45deg);
    top: 7px;
}
.navbar-toggler[aria-expanded=true] .toggler-icon:nth-of-type(2) {
    opacity: 0;
}
.navbar-toggler[aria-expanded=true] .toggler-icon:nth-of-type(3) {
    transform: rotate(135deg);
    top: -7px;
}
#root{
    overflow: hidden;
}
.navbar-nav .nav-item a.active, .navbar-nav .nav-item a:hover {
    color: #E44E83;
}
.ml-auto{
    margin-left: auto !important;
}
.navbar-nav .nav-item .nav-link{
    color: #fff;
    font-weight: 600;
}
@media screen and (min-width:992px)
{
    .navbar-nav .nav-item .nav-link{
        padding-left: 1.5rem !important;
        padding-right: 1.5rem !important;
    }
}
@media screen and (max-width: 991.98px)
{
    .navbar-collapse {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        background-color: #fff;
        z-index: 9;
        box-shadow: 0px 15px 20px 0px rgb(0 0 0 / 10%);
        padding: 5px 12px;
        border-radius: 7px;
    }
    .navbar-nav .nav-item .nav-link{
        color:rgba(7, 6, 6, 0.863);
    }
}