
.hero-section {
    padding-top: 300px;
    padding-bottom: 240px;
    background-color: #F4F7FC;
}
.img-bg {
    background: url('/src/images/hero-bg.svg');
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    width: 100%;
    height: 100%;
}

.hero-section .hero-content-wrapper h1 {
    font-size: 55px;
    line-height: 65px;
    font-weight: 800;
    margin-bottom: 20px;
    color: #fff;
}

.hero-section .hero-content-wrapper p {
    margin-bottom: 25px;
    color: #fff;
    padding-right: 30px;
}
.theme-btn {
    display: inline-block;
    font-weight: 600;
    text-align: center;
    text-decoration: none;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    padding: 17px 44px;
    font-size: 20px;
    border-radius: 50px;
    color: #313450;
    cursor: pointer;
    z-index: 5;
    transition: all .4s ease-in-out;
    border: none;
    background: #fff;
}
.theme-btn:hover, .theme-btn:focus {
    background: #E44E83;
    color: #fff;
}

.hero-section .hero-img {
    position: relative;
    z-index: 1;
}

.hero-section .hero-img .shape {
    position: absolute;
    bottom: -60px;
    left: -30px;
    z-index: -1;
}


@media only screen and (min-width: 768px) and (max-width: 991px)
{
    .hero-section {
        padding-top: 250px;
        padding-bottom: 210px;
    }
}
@media (max-width: 767px)
{
    .hero-section {
        padding-top: 200px;
        padding-bottom: 160px;
    }
}