.testimonial-section .container {
    position: relative;
    z-index: 1;
}
.testimonial-section .container .shape {
    position: absolute;
    left: -50px;
    bottom: -30px;
    z-index: -1;
}
.pb-100 {
    padding-bottom: 100px;
}
.pt-100 {
    padding-top: 100px;
}

.testimonial-section .testimonial-active-wrapper {
    position: relative;
}

.testimonial-section .single-testimonial {
    padding: 35px;
    border-radius: 10px;
    background: #fff;
    margin-bottom: 60px;
}

.testimonial-section .single-testimonial .testimonial-info-wrapper {
    display: flex;
    margin-bottom: 25px;
    justify-content: space-between;
}
.testimonial-section .single-testimonial .testimonial-info-wrapper .testimonial-info {
    display: flex;
    align-items: center;
}
.testimonial-section .single-testimonial .testimonial-info-wrapper .testimonial-info .testimonial-img {
    margin-right: 20px;
}
.testimonial-section .single-testimonial .testimonial-info-wrapper .testimonial-info .testimonial-meta h5 {
    font-weight: 600;
    color: #313450;
    margin: 0px;
}
.testimonial-section .single-testimonial .testimonial-info-wrapper .testimonial-info .testimonial-meta span {
    font-size: 16px;
    color: #313450;
}
.testimonial-section .single-testimonial .testimonial-info-wrapper .rating {
    padding-right: 40px;
    color: #E44E83;
}

.testimonial-section .single-testimonial .testimonial-info-wrapper .rating i {
    font-size: 14px;
    margin: 0 4px;
}

.owl-theme .owl-nav{
    display: none;
}


.testimonial-active-wrapper .owl-theme .owl-dots .owl-dot span {
    width: 15px;
    height: 15px;
    background: rgba(228, 78, 131, 0.5);
}
.testimonial-active-wrapper .owl-theme .owl-dots .owl-dot:hover span {
    background: rgba(228, 78, 131, 0.5);
}
.testimonial-active-wrapper .owl-theme .owl-dots .owl-dot.active span{
    background: #E44E83;
}

@media (max-width: 767px)
{
    .testimonial-section .single-testimonial .testimonial-info-wrapper {
        flex-direction: column;
    }
    .testimonial-section .single-testimonial .testimonial-info-wrapper .rating {
        margin-top: 15px;
    }
}